/* ###### Calendar  ###### */



/* classes attached to <body> */
.fc-not-allowed {
  /* override events' custom cursors */
  cursor: not-allowed;
}

.fc-not-allowed .fc-event {
  /* override events' custom cursors */
  cursor: not-allowed;
}

.fc-unselectable {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.fc {
  /* layout of immediate children */
  display: flex;
  flex-direction: column;
  font-size: 1em;
}

.fc table {
  border-collapse: collapse;
  border-spacing: 0;
  font-size: 1em;
  /* normalize cross-browser */
}

.fc th {
  text-align: center;
  vertical-align: top;
  padding: 0;
}

.fc td {
  vertical-align: top;
  padding: 0;
}

.fc a[data-navlink] {
  cursor: pointer;
}

