  height: 0;
  border: 4px solid var(--primary-bg-color);
  border: calc(var(--fc-daygrid-event-dot-width, 8px) / 2) solid var(--fc-event-border-color, var(--primary-bg-color));
  border-radius: 4px;
  border-radius: calc(var(--fc-daygrid-event-dot-width, 8px) / 2);
}

/* --- spacing between time and title --- */
.fc-direction-ltr .fc-daygrid-event .fc-event-time {
  margin-right: 3px;
}

.fc-direction-rtl .fc-daygrid-event .fc-event-time {
  margin-left: 3px;
}

/*
A VERTICAL event
*/
.fc-v-event {
  /* allowed to be top-level */
  display: block;
  border: 1px solid var(--primary-bg-color);
  border: 1px solid var(--fc-event-border-color, var(--primary-bg-color));
  background-color: var(--primary-bg-color);
  background-color: var(--fc-event-bg-color, var(--primary-bg-color));
}

.fc-v-event .fc-event-main {
  color: #ffffff;
  color: var(--fc-event-text-color, #ffffff);
  height: 100%;
}

.fc-v-event .fc-event-main-frame {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.fc-v-event .fc-event-time {
  flex-grow: 0;
  flex-shrink: 0;
  max-height: 100%;
  overflow: hidden;
}

.fc-v-event .fc-event-title-container {
  /* a container for the sticky cushion */
  flex-grow: 1;
  flex-shrink: 1;
  min-height: 0;
  /* important for allowing to shrink all the way */
}

.fc-v-event .fc-event-title {
  /* will have fc-sticky on it */
  top: 0;
  bottom: 0;
  max-height: 100%;
  /* clip overflow */
  overflow: hidden;
}

.fc-v-event:not(.fc-event-start) {
  border-top-width: 0;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

.fc-v-event:not(.fc-event-end) {
  border-bottom-width: 0;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.fc-v-event.fc-event-selected:before {
  /* expand hit area */
  left: -10px;
  right: -10px;
}

.fc-v-event .fc-event-resizer-start {
  cursor: n-resize;
}

.fc-v-event .fc-event-resizer-end {
  cursor: s-resize;
}

.fc-v-event:not(.fc-event-selected) .fc-event-resizer {
  height: 8px;
  height: var(--fc-event-resizer-thickness, 8px);
  left: 0;
  right: 0;
}

.fc-v-event:not(.fc-event-selected) .fc-event-resizer-start {
  top: -4px;
  top: calc(var(--fc-event-resizer-thickness, 8px) / -2);
}

.fc-v-event:not(.fc-event-selected) .fc-event-resizer-end {
  bottom: -4px;
  bottom: calc(var(--fc-event-resizer-thickness, 8px) / -2);
}

.fc-v-event.fc-event-selected .fc-event-resizer {
  left: 50%;
  margin-left: -4px;
  margin-left: calc(var(--fc-event-resizer-dot-total-width, 8px) / -2);
}

.fc-v-event.fc-event-selected .fc-event-resizer-start {
  top: -4px;
  top: calc(var(--fc-event-resizer-dot-total-width, 8px) / -2);
}

.fc-v-event.fc-event-selected .fc-event-resizer-end {
  bottom: -4px;
  bottom: calc(var(--fc-event-resizer-dot-total-width, 8px) / -2);
}

.fc .fc-timegrid .fc-daygrid-body {
  /* the all-day daygrid within the timegrid view */
  z-index: 2;
  /* put above the timegrid-body so that more-popover is above everything. TODO: better solution */
}

.fc .fc-timegrid-divider {
  padding: 0 0 2px;
  /* browsers get confused when you set height. use padding instead */
}

.fc .fc-timegrid-body {
  position: relative;
  z-index: 1;
  /* scope the z-indexes of slots and cols */
  min-height: 100%;
  /* fill height always, even when slat table doesn't grow */
}

.fc .fc-timegrid-axis-chunk {
  /* for advanced ScrollGrid */
  position: relative;
}

.fc .fc-timegrid-axis-chunk>table {
  position: relative;
  z-index: 1;
  /* above the now-indicator-container */
}

.fc .fc-timegrid-slots {
  position: relative;
  z-index: 1;
}

.fc .fc-timegrid-slot {
  /* a <td> */
  height: 1.5em;
  border-bottom: 0;
}

.fc .fc-timegrid-slot:empty:before {
  content: "";
  /* make sure there's at least an empty space to create height for height syncing */
}

.fc .fc-timegrid-slot-minor {
  border-top-style: dotted;
}

.fc .fc-timegrid-slot-label-cushion {
  display: inline-block;
  white-space: nowrap;
}

.fc .fc-timegrid-slot-label {
  vertical-align: middle;
  /* vertical align the slots */
}

.fc .fc-timegrid-axis-cushion,
.fc .fc-timegrid-slot-label-cushion {
  padding: 0 4px;
}

.fc .fc-timegrid-axis-frame-liquid {
  height: 100%;
  /* will need liquid-hack in FF */
}

.fc .fc-timegrid-axis-frame {
  overflow: hidden;
  display: flex;
  align-items: center;
  /* vertical align */
  justify-content: flex-end;
  /* horizontal align. matches text-align below */
}

.fc .fc-timegrid-axis-cushion {
  max-width: 60px;
  /* limits the width of the "all-day" text */
  flex-shrink: 0;
  /* allows text to expand how it normally would, regardless of constrained width */
}

.fc-direction-ltr .fc-timegrid-slot-label-frame {
  text-align: right;
}

.fc-direction-rtl .fc-timegrid-slot-label-frame {
  text-align: left;
}

.fc-liquid-hack .fc-timegrid-axis-frame-liquid {
  height: auto;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.fc .fc-timegrid-col.fc-day-today {
  background-color: rgba(255, 220, 40, 0.15);
  background-color: var(--fc-today-bg-color, rgba(255, 220, 40, 0.15));
}

.fc .fc-timegrid-col-frame {
  min-height: 100%;
  /* liquid-hack is below */
  position: relative;
}

.fc-liquid-hack .fc-timegrid-col-frame {
  height: auto;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.fc-media-screen .fc-timegrid-cols {
  position: absolute;
  /* no z-index. children will decide and go above slots */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.fc-media-screen .fc-timegrid-cols>table {
  height: 100%;
}

.fc-media-screen .fc-timegrid-col-bg,
.fc-media-screen .fc-timegrid-col-events,
.fc-media-screen .fc-timegrid-now-indicator-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.fc-media-screen .fc-timegrid-event-harness {
  position: absolute;
  /* top/left/right/bottom will all be set by JS */
}

.fc .fc-timegrid-col-bg {
  z-index: 2;
  /* TODO: kill */
}

.fc .fc-timegrid-col-bg .fc-non-business {
  z-index: 1;
}

.fc .fc-timegrid-col-bg .fc-bg-event {
  z-index: 2;
}

.fc .fc-timegrid-col-bg .fc-highlight {
  z-index: 3;
}

.fc .fc-timegrid-bg-harness {
  position: absolute;
  /* top/bottom will be set by JS */
  left: 0;
  right: 0;
}

.fc .fc-timegrid-col-events {
  z-index: 3;
  /* child event segs have z-indexes that are scoped within this div */
}

.fc .fc-timegrid-now-indicator-container {
  bottom: 0;
  overflow: hidden;
  /* don't let overflow of lines/arrows cause unnecessary scrolling */
  /* z-index is set on the individual elements */
}

.fc-direction-ltr .fc-timegrid-col-events {
  margin: 0 2.5% 0 2px;
}

.fc-direction-rtl .fc-timegrid-col-events {
  margin: 0 2px 0 2.5%;
}

.fc-timegrid-event-harness-inset .fc-timegrid-event {
  box-shadow: 0px 0px 0px 1px #ffffff;
  box-shadow: 0px 0px 0px 1px var(--fc-page-bg-color, #ffffff);
}

.fc-timegrid-event {
  /* events need to be root */
  font-size: 0.85em;
  font-size: var(--fc-small-font-size, 0.85em);
  border-radius: 3px;
}

.fc-timegrid-event.fc-event-mirror {
  box-shadow: 0px 0px 0px 1px #ffffff;
  box-shadow: 0px 0px 0px 1px var(--fc-page-bg-color, #ffffff);
}

.fc-timegrid-event .fc-event-main {
  padding: 1px 1px 0;
}

.fc-timegrid-event .fc-event-time {
  white-space: nowrap;
  font-size: 0.85em;
  font-size: var(--fc-small-font-size, 0.85em);
  margin-bottom: 1px;
}

.fc-timegrid-event-condensed .fc-event-main-frame {
  flex-direction: row;
  overflow: hidden;
}

.fc-timegrid-event-condensed .fc-event-time:after {
  content: "-";
  /* dash surrounded by non-breaking spaces */
}

.fc-timegrid-event-condensed .fc-event-title {
  font-size: 0.85em;
  font-size: var(--fc-small-font-size, 0.85em);
}

.fc-media-screen .fc-timegrid-event {
  position: absolute;
  /* absolute WITHIN the harness */
  top: 0;
  bottom: 1px;
  /* stay away from bottom slot line */
  left: 0;
  right: 0;
}

.fc .fc-timegrid-now-indicator-line {
  position: absolute;
  z-index: 4;
  left: 0;
  right: 0;
  border-style: solid;
  border-color: red;
  border-color: var(--fc-now-indicator-color, red);
  border-width: 1px 0 0;
}

.fc .fc-timegrid-now-indicator-arrow {
  position: absolute;
  z-index: 4;
  margin-top: -5px;
  /* vertically center on top coordinate */
  border-style: solid;
  border-color: red;
}

.fc-direction-ltr .fc-timegrid-now-indicator-arrow {
  left: 0;
  /* triangle pointing right. TODO: mixin */
  border-width: 5px 0 5px 6px;
  border-top-color: transparent;
  border-bottom-color: transparent;
}

.fc-direction-rtl .fc-timegrid-now-indicator-arrow {
  right: 0;
  /* triangle pointing left. TODO: mixin */
  border-width: 5px 6px 5px 0;
  border-top-color: transparent;
  border-bottom-color: transparent;
}

:root {
  --fc-list-event-dot-width: 10px;
  --fc-list-event-hover-bg-color: #f1f5ff;
}

.fc-theme-standard .fc-list {
  border: 1px solid #ebf1ff;
}

.fc .fc-list-empty {
  background-color: #f6f6f9;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  /* vertically aligns fc-list-empty-inner */
}

.fc .fc-list-empty-cushion {
  margin: 5em 0;
}

.fc .fc-list-table {
  width: 100%;
  border-style: hidden;
  /* kill outer border on theme */
}

.fc .fc-list-table tr>* {
  border-left: 0;
  border-right: 0;
}

.fc .fc-list-sticky .fc-list-day>* {
  /* the cells */
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background: #ffffff;
  background: var(--fc-page-bg-color, #ffffff);
  /* for when headers are styled to be transparent and sticky */
}

.fc .fc-list-table th {
  padding: 0;
  /* uses an inner-wrapper instead... */
}

.fc .fc-list-table td {
  padding: 8px 14px;
}

.fc .fc-list-day-cushion {
  padding: 8px 14px;
}

.fc .fc-list-day-cushion:after {
  content: "";
  clear: both;
  display: table;
  /* clear floating */
}

.fc-theme-standard .fc-list-day-cushion {
  background-color: #f6f6f9;
}

.fc-direction-ltr .fc-list-day-text,
.fc-direction-rtl .fc-list-day-side-text {
  float: left;
}

.fc-direction-ltr .fc-list-day-side-text,
.fc-direction-rtl .fc-list-day-text {
  float: right;
}

/* make the dot closer to the event title */
.fc-direction-ltr .fc-list-table .fc-list-event-graphic {
  padding-right: 0;
}

.fc-direction-rtl .fc-list-table .fc-list-event-graphic {
  padding-left: 0;
}

.fc .fc-list-event.fc-event-forced-url {
  cursor: pointer;
  /* whole row will seem clickable */
}

.fc .fc-list-event:hover td {
  background-color: #f1f5ff;
  color: var(--primary-bg-color);
}

.fc .fc-list-event-graphic,
.fc .fc-list-event-time {
  white-space: nowrap;
  width: 1px;
}

.fc .fc-list-event-dot {
  display: inline-block;
  box-sizing: content-box;
  width: 0;
  height: 0;
  border: 5px solid var(--primary-bg-color);
  border: calc(var(--fc-list-event-dot-width, 10px) / 2) solid var(--fc-event-border-color, var(--primary-bg-color));
  border-radius: 5px;
  border-radius: calc(var(--fc-list-event-dot-width, 10px) / 2);
}

.fc .fc-list-event-title a {
  color: inherit;
  text-decoration: none;
}

.fc .fc-list-event.fc-event-forced-url:hover a {
  text-decoration: underline;
}

.fc-theme-bootstrap a:not([href]) {
  color: inherit;
  /* natural color for navlinks */
}

#external-events {
  height: 10%;
  margin-top: 3.5rem;
  padding: 0 20px;
  border: 1px solid #ece9fb;
  background: #f7f6fd;
  text-align: left;
  border-radius: 5px;
}

#external-events h4 {
  font-size: 16px;
  margin-top: 0;
  padding-top: 1em;
}

#external-events .fc-event {
  margin: 11px 0;
  cursor: move;
}

#external-events p {
  margin: 1.5em 0;
  font-size: 11px;
  color: #666;
}

#external-events p input {
  margin: 0;
  vertical-align: middle;
}

.fc-daygrid-day-number {
  color: #1d212f;
}

@media (max-width: 767px) {
  .fc .fc-toolbar {
      display: block !important;
      text-align: center;
  }

  .fc-toolbar-chunk {
      margin: 17px 0;
  }
}

@media (max-width: 1200px) {
  #external-events {
      margin-top: 0 !important;
      margin-bottom: 1rem;
  }
}